<template>
  <div class="addBankCard">
    <div class="wrapper b-transparent">
      <!--  start -->
      <headerOrderInfo></headerOrderInfo>
      <!--  --> 
      <div class="bankCard icon pl12 bt bb pr" :class='{"bgEBEBE4":!useableBankCards.length,"bgfff":useableBankCards.length}'>   <!--  -->
        <div class="dis-inline ver-t mt4">
          <img  :src='this.contextPath+"/static/images/bank-logo/"+bankCardFirst.bankCode+".png"' width="16" height="16" alt="banklogo">
          <!-- <img :src='"../../static/images/bank-logo/"+bankCardFirst.bankCode+".png"' width="16" height="16" alt="banklogo"> -->
        </div>
        <div class="dis-inline">
          <p class="fs14 c666 mb10">{{bankCardFirst.bankName}}</p>
          <i class="fs14 c666 mr10">{{bankCardFirst.cardlater}}</i>
          <i class="fs12 c999 mt10" v-if="bankCardFirst.cardtype == 'DEBIT'"></i>
          <i class="fs12 c999 mt10" v-if="bankCardFirst.cardtype == 'CREDIT'"></i>
        </div>
        <span class="change" @click="showBankCardTip"></span>
      </div>
    </div>
    <div class="pt10 pb10 pl12">
      <p class="fs14 c999">
      <label for="agreement">
        <input class="hide" id="agreement" type="checkbox" :checked="agree" v-model="agree">
        <i class="icon agreement"></i>
      </label><a class="c2eb944 pr protocal" @click="showProtocal"></a></p>
    </div>
    <div class="pl12 pr12 mt10"> 
      <button class="w" :disabled="isDisable" id="submit" type="button" @click="authorizationOrder">
        <i></i>
      </button>
    </div>
    <!--   -->
    <foot class="foot"></foot>
    <!--  -->
    <!--  -->
    <div class="bank-tip" v-show="isShowBankTip">
        <h2><i class="icon pa fs24 close" @click="closeBBankTip">&#xe60f;</i></h2> <!--  bankCardInfo -->
        <div class="bank-content">
       
        <!--   start -->
          <div class="bt pt10 pb10" v-for="bank of useableBankCards" @click="getBankInfo(bank)">
            <div class="dis-inline ver-mid pl20"><img :src='this.contextPath+"/static/images/bank-logo/"+bank.bankCode+".png"' width="30" height="30" alt="banklogo"></div>
            <div class="dis-inline ver-mid">
              <p class="fs12 c666">{{bank.bankName}}({{bank.cardlater}})</p>
              <i class="fs12 c999 mt10" v-if="bank.cardtype == 'DEBIT'"></i>
              <i class="fs12 c999 mt10" v-if="bank.cardtype == 'CREDIT'"></i>
            </div>
          </div>
          <!--  end -->
          <!--  start -->
          <div class="bt pt10 pb10 bgf5f5f5" v-for="bank of unuseableBankCards">
            <div class="dis-inline ver-mid pl20"><img :src='this.contextPath+"/static/images/bank-logo/"+bank.bankCode+".png"' width="30" height="30" alt="banklogo"></div>
            <div class="dis-inline ver-mid">
              <p class="fs12 c666">{{bank.bankName}}({{bank.cardlater}})</p>
              <i class="fs12 c999 mt10" v-if="bank.cardtype == 'DEBIT'"></i>
              <i class="fs12 c999 mt10" v-if="bank.cardtype == 'CREDIT'"></i>
              <i class="fs12 c999 mt10">{{bank.unusableCause}}</i>
            </div>
          </div>
          <!--  end -->
        </div>
        <router-link v-if="showChangeCard" tag="div" class="bt add fs16 li" :to="{name: 'EnterCardNo',path:'/enterCardNo'}">
            <div class="dis-inline ver-mid">
              <i class="icon addSymbol fs20 c999">&#xe678;</i>
              <i class="c333 ml6"></i>
            </div>
        </router-link>
    </div>
    <!--  START-->
    <shortMessage :isShowVerify="isShowVerify"></shortMessage>
    <!--  END-->
    <!--  START-->
    <Protocal :isProtocal="isProtocal"></Protocal>
    <!--  END-->
    <!--  -->
    <!--   -->
    <div class="bg-fixed" v-show="isShowMask" id="mask" @click="mask"></div>
    <!--  -->
  </div>
</template>
<script>
import headerOrderInfo from '@/components/header-order-info.vue'
import foot from '@/components/foot.vue'
import AddItem from '@/views/addItem.vue'
let ERR_OK = 'success'; //
export default {
  name: 'AddBankCard',
  data () {
    return {
      isProtocal:false, //
      isShowBankTip:false, //
      isShowMask: false,  //
      bankCardFirst:{}, //{"bankCode":"ABC","bankName":"ff","cardtype":"","number":"2218"} 
      bankCardInfo:[], //  [{"bankCode":"ABC","bankName":"ff","cardtype":"","number":"2218"}]
      useableBankCards:[],
      unuseableBankCards:[],
      isShowVerify: false, //
      smsCode: '', //
      agree: true,//
      contextPath:contextPath, // 
      isAuthorizationPay:'failed',//
      showChangeCard:showChangeCard // 
      // smsType:"dddsms"//   
    }
  },
  computed:{  //disabled
    isDisable:function(){
      return !this.agree || !useableBankCards.length
    }
  },
  watch:{
    isAuthorizationPay(){
      var self = this
      this.$bus.$on("isAuthorizationPay",function(msg){
        self.isAuthorizationPay = msg[0]
        if(self.isAuthorizationPay === 'success'){
          // var timer = window.sessionStorage.getItem("timeId");
          // window.clearTimeout(timer)
           self.authorizationPay(msg[1]);  //
        }
      })
    }
  },
  created() {
    var self = this
    this.hideTip() //
    this.$bus.$on("protocal",function(msg){ //bus
      self.isProtocal = msg
      self.isShowMask = msg
    })
    this.$bus.$on("agree",function(msg){   //bus
      self.agree = msg
    })
    this.$bus.$on("close",function(msg){   //bus
      self.isShowMask = msg;
      self.isShowVerify = msg;
      
    })
    //
    this.useableBankCards = useableBankCards
    this.unuseableBankCards = unuseableBankCards
    if(useableBankCards.length){
      this.bankCardFirst = useableBankCards[0] 
    }else{
      this.bankCardFirst = unuseableBankCards[0]
    }
  },
  methods:{
    authorizationOrder(){  //
      var self = this
      var param={}
          param.token = token
          param.bindId = this.bankCardFirst.bindid
      this.$http.get(Request.url+'/wap/preauth/ajax/bindOrder',{params:param}).then((res) => {
        var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body 
        if(data.bizStatus == ERR_OK){ 
           if(data.smsType){
              this.$store.state.smsType = data.smsType  //
           } 
           if(data.requiredCardItem){  //
              this.$store.state.cardItemNecessary = data.requiredCardItem //
              this.$store.state.cardInfo = data.autoFillItem //
              this.$router.push({ name: 'AddItem',path:'/AddItem',component: AddItem,params:{'token':token,'bindId':param.bindId}}) // 
           }else{   //
              if(data.smsType){ //
                this.showSms()//
                this.smsSend()
              }else{//
                this.authorizationPay()
              }
           }
          this.$store.commit("allData")
        }else{   //
          this.errorProcess(data.errorMsg,data.errorCode)
        }
      })
    },
    authorizationPay(sms){ //  ps:
      var self = this
      var param = {}
          param.token = token
          if(sms){  //
            param.verifyCode = sms
          }
      this.$http.get(Request.url+'/wap/preauth/ajax/bindConfirm',{params:param}).then((res) => {
        var data = (typeof res.body == 'string') ? JSON.parse(res.body) : res.body 
        if(data.bizStatus == ERR_OK){  
          window.location.href = this.contextPath+"/wap/query/result?token="+token  //
        }else{   //
          if(data.bizStatus === 'smsFailed'){
              this.smsErrorTip(data.errorMsg,data.errorCode)//  
           }else{
              window.location.href = this.contextPath+"/wap/query/result?token="+token  //
           }
        }
      })
    },
    getBankInfo(bank){
        this.bankCardFirst = bank
        this.closeBBankTip()
    },
    showBankCardTip(){
      this.isShowMask = true
      this.isShowBankTip = true
    },
    closeBBankTip() {
      this.isShowMask = false
      this.isShowBankTip = false
    }
  },
  components:{
    headerOrderInfo,foot,
    Protocal:function(resolve){ require(['@/components/protocal.vue'],resolve)},
    shortMessage:function(resolve){ require(['@/components/shortMessage.vue'],resolve)}
  }
}
</script>
<style scoped lang="less">
#submit[disabled]{
  background: #ccc;
}
.bankCard{
  height:60px;
  padding-top: 14px;
  .change{
    position: absolute;
    right:12px;
    top:36%;
    width:54px;
    height:18px;
    line-height:18px;
    font-size: 14px;
    color:#2fbf45;
    text-align: right;
    background: url("../../static/pre-authorization-static/assets/images/change-icon.png") no-repeat left ;
    background-size: 20px 16px;
  }
}
#agreement:checked +.icon.agreement{
  &:before{
    display: inline-block;
    width:18.4px;
    height:18.4px;
    line-height: 18.4px;
    content:"\e7c0";
    font-size:16px;
    color:#2eb944;
    vertical-align: top;
  }
}
#agreement +.icon.agreement{
  &:before{
    display: inline-block;
    width:18.4px;
    height:18.4px;
    line-height: 18.4px;
    content:"\e7c1";
    font-size:16px;
    color:#ccc;
    vertical-align: top;
  }
}
.bank-tip{   //
  width:88%;
  background: #fff;
  position: fixed;
  top:40%;
  left:50%;
  margin-left:-44%;
  margin-top:-130px;
  border-radius:4px;
  transition:opacity 0.5s;
  z-index:11;
  h2{
    padding:10px;
    height:20px;
    font-weight: 700;
    font-size: 16px;
  }
  .close{
    height:40px;
    line-height:40px;
    padding:0 10px;
    right:0px; top:0;
  }
  .bank-content{
    max-height: 290px;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
  } 
  .add{
      padding:14px 12px;
      a{
        color:#666;
      }
    }
  .addSymbol{
    width:30px;height:30px;
    text-align: center;
    display: inline-block;
  }
}   //
.foot{width:60%;position: fixed;bottom:30px;left:50%;margin-left:-30%}
</style>



