<template>
  <div id="protocal">
     <!--  -->
    <div  class="pme-popup pme-protocol scroll" v-show="isProtocal"><h2><span class="close" @click="closeProtocal"></span></h2><div class="content-wrapper" style="height: 400px;"><div class="content"><p></p><p>4001-500-800</p> <p></p> <p></p> <p></p> <p></p> <p>//</p> <p></p> <p></p> <p></p> <p></p><p></p><p></p><p>1</p> <p>2</p><p>3/</p> <p>4</p><p>(5)</p><p></p> <p></p> <p></p> <p></p><p></p><p></p><p></p><p></p><p></p><p></p></div></div></div>
  </div>
</template>
<script>
export default {
  name: 'Protocal',
  props:{
     isProtocal:false
  },
  methods:{
    closeProtocal() {  //  
      this.$bus.$emit('protocal',false)   //isProtocal   
      this.$bus.$emit('agree',true)   //
    }
  }
}
</script>
<style scoped lang="less">
.protocal{z-index:9;}
.pme-popup.scroll {
  // top: 30px;
  padding-bottom: 4px;
  overflow: hidden;
}

.pme-popup.scroll .content-wrapper {
  overflow-y: scroll;
  -webkit-transform: translateZ(0);
  -webkit-overflow-scrolling: touch;
}

.pme-protocol {
  height: auto;
  font-size: 14px;
  color: #333333;
  position: fixed;
  top: 30px;
  z-index: 11;
  left: 30px;
  right: 30px;
  border-radius: 4px;
  box-shadow: #b3b3b3 1px 1px 5px, #b3b3b3 -1px -1px 5px;
  background-color: #ffffff;
  line-height: 1.42857;
}

.pme-protocol h2 {
  position: relative;
  height: 15px;
  color: #4490de;
  margin: 0;
  padding: 10px;
  font-size: 16px;
  background-color: #ffffff;
  border-bottom: #4490de solid 2px;
  border-radius: 4px 4px 0 0;
}

.pme-protocol h2 .close {
  position: absolute;
  top: 10px;
  right: 10px;
  color: #4490de;
}

.pme-protocol .content-wrapper {
  padding: 0 10px;
  outline: none;
}

.pme-protocol p {
  text-indent: 2em;
} 
</style>


