import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'
import vueEventBus from 'vue-event-bus'
import EnterCardNo from '@/views/enterCardNo'   //  
import SupportBank from '@/views/supportBank'   //
import AddBankCard from '@/views/addBankCard'  //
import BankCardList from '@/views/bankCardList'  //  
import AddItem from '@/views/addItem'  //
import Home from '@/views/home'  //  
Vue.use(Router)
Vue.use(Resource)
Vue.use(vueEventBus)
const router = new Router({
  routes: [  
    {
      path: '/',
      name: 'Home',   //   
      component: Home
    }, 
    {
      path: '/SupportBank',   //
      name: 'SupportBank',
      component: SupportBank
    },
    {
      path: '/EnterCardNo',   //
      name: 'EnterCardNo',
      component: EnterCardNo
    },
    {
      path: '/AddBankCard/:cardNo/:token',  //
      name: 'AddBankCard',
      component: AddBankCard
    },
    {
      path: '/AddItem/:token/:bindId',  //
      name: 'AddItem',
      component: AddItem
    }
  ]
})
export default router;
 




